import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  CircularProgress,
  Grid,
  IconButton,
  Button,
  Alert,
  Container
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import { useAuth } from '../contexts/AuthContext';
import { apiService, baseStaticURL } from '../services/api';
import { Refresh as RefreshIcon } from '@mui/icons-material';

function Home() {
  console.log('Home component rendered');
  const navigate = useNavigate();
  const { user } = useAuth();
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [testStatus, setTestStatus] = useState(null);

  useEffect(() => {
    console.log('Home useEffect triggered');
    loadPosts();
  }, []);

  const loadPosts = async () => {
    console.log('loadPosts started');
    setLoading(true);
    setError('');
    try {
      const data = await apiService.postService.getPosts();
      console.log('API response data:', data);
      if (data.data && Array.isArray(data.data.list)) {
        setPosts(data.data.list);
        console.log('Posts loaded successfully, count:', data.data.list.length);
      } else {
        console.error('API response data format incorrect:', data);
        setError('加载帖子失败: 数据格式错误');
      }
    } catch (err) {
      console.error('Error loading posts:', err);
      setError(err.message || '加载帖子失败');
    } finally {
      setLoading(false);
      console.log('loadPosts finished, loading:', false);
    }
  };

  const handleDelete = async (id, event) => {
    event.stopPropagation(); // Prevent navigation when clicking delete button
    if (window.confirm('确定要删除这条信息吗？')) {
      try {
        await apiService.postService.deletePost(id);
        setPosts(posts.filter(post => post._id !== id));
      } catch (err) {
        setError(err.message || '删除失败');
      }
    }
  };

  // 测试服务器连接
  const testServerConnection = async () => {
    try {
      setTestStatus({ type: 'info', message: '正在测试服务器连接...' });
      const response = await apiService.testConnection();
      setTestStatus({ type: 'success', message: '服务器连接正常！' });
      console.log('服务器响应:', response);
    } catch (error) {
      console.error('服务器连接测试失败:', error);
      setTestStatus({ 
        type: 'error', 
        message: `连接失败: ${error.message || '未知错误'}`
      });
    }
  };

  console.log('Home rendering, loading:', loading, 'error:', error, 'posts count:', posts.length);

  return (
    <Container maxWidth="lg" sx={{ py: 3 }}>
      <Box sx={{ mb: 3 }}>
        <Box sx={{ 
          display: 'flex', 
          flexDirection: { xs: 'column', sm: 'row' },
          justifyContent: 'space-between', 
          alignItems: { xs: 'stretch', sm: 'center' },
          gap: 2
        }}>
          <Typography variant="h4" component="h1">
            首页
          </Typography>
          <Button
            variant="contained"
            color="primary"
            startIcon={<RefreshIcon />}
            onClick={testServerConnection}
            fullWidth={false}
            sx={{ 
              minWidth: { xs: '100%', sm: 120 },
              height: 48
            }}
          >
            测试连接
          </Button>
        </Box>

        {testStatus && (
          <Alert severity={testStatus.type} sx={{ mt: 2 }}>
            {testStatus.message}
          </Alert>
        )}

        {error && (
          <Alert severity="error" sx={{ mt: 2 }}>
            {error}
          </Alert>
        )}
      </Box>

      {loading && posts.length === 0 ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <CircularProgress />
        </Box>
      ) : posts.length === 0 ? (
        <Typography align="center" sx={{ mt: 4 }}>
          没有找到相关信息。
        </Typography>
      ) : (
        <Grid container spacing={2}>
          {posts.map((post) => (
            <Grid item xs={12} sm={6} md={4} key={post._id}>
              <Card
                sx={{ 
                  height: '100%', 
                  display: 'flex', 
                  flexDirection: 'column',
                  cursor: 'pointer',
                  position: 'relative',
                  '&:hover': {
                    boxShadow: 3
                  }
                }}
                onClick={() => navigate(`/detail/${post._id}`)}
              >
                {user?.role === 'admin' && (
                  <IconButton
                    sx={{
                      position: 'absolute',
                      top: 8,
                      right: 8,
                      zIndex: 1,
                      backgroundColor: 'rgba(255, 255, 255, 0.8)',
                      '&:hover': {
                        backgroundColor: 'rgba(255, 255, 255, 0.9)',
                      },
                    }}
                    onClick={(e) => handleDelete(post._id, e)}
                    size="small"
                  >
                    <DeleteIcon fontSize="small" />
                  </IconButton>
                )}
                {post.imageUrls && post.imageUrls.length > 0 ? (
                  <Box
                    component="img"
                    src={`${baseStaticURL}${post.imageUrls[0]}`}
                    alt={post.title}
                    sx={{
                      width: '100%',
                      height: 200,
                      objectFit: 'cover',
                    }}
                  />
                ) : (
                  <Box
                    component="img"
                    src="https://via.placeholder.com/150"
                    alt="Placeholder"
                    sx={{
                      width: '100%',
                      height: 200,
                      objectFit: 'cover',
                    }}
                  />
                )}
                <CardContent sx={{ flexGrow: 1 }}>
                  <Typography gutterBottom variant="h6" component="div" noWrap>
                    {post.title}
                  </Typography>
                  {post.loanAmount > 0 && (
                    <Typography variant="body2" color="text.secondary">
                      价格: ¥{post.loanAmount.toFixed(2)}
                    </Typography>
                  )}
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      )}
    </Container>
  );
}

export default Home; 