import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  CircularProgress,
  // List,
  // ListItem,
  // ListItemText,
  Paper,
  Card,
  CardContent,
  IconButton,
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close'; // Import CloseIcon
import apiService, { baseStaticURL } from '../services/api'; // Uncomment if needed for general notifications

function Notification() {
  const [notifications, setNotifications] = useState([]); // State for general notifications
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  // Function to request notification permission
  const requestNotificationPermission = async () => {
    if (!('Notification' in window) || !('requestPermission' in Notification) || typeof Notification.requestPermission !== 'function') {
      console.warn('This browser does not support desktop notification or permission request method.');
      return; // Exit if API is not fully supported
    }

    if (Notification.permission !== 'granted') {
      console.log('Requesting notification permission...');
      const permission = await Notification.requestPermission();
      console.log('Notification permission result:', permission);
      // If the user accepts, then we can send a notification
      if (permission === 'granted') {
        console.log('Notification permission granted.');
        // Optionally show a welcome notification here
        // new Notification('通知已开启', { body: '您将收到新的通知提醒。' });
      } else {
        console.warn('Notification permission denied.');
      }
    }
  };

  // Function to trigger system notifications for new unread notifications
  const triggerSystemNotifications = async (fetchedNotifications) => {
    if (Notification.permission === 'granted' && Array.isArray(fetchedNotifications)) {
      console.log('Checking for unread notifications to trigger system notifications...');
      // Find unread notifications that haven't been shown as system notifications yet
      // For simplicity initially, let's assume all newly fetched notifications that are not read are considered new for system notification purpose.
      // A more robust solution would track shown notifications e.g., using local storage or backend flag.

      const unreadNotifications = fetchedNotifications.filter(notif => !notif.isRead);

      console.log(`Found ${unreadNotifications.length} unread notifications.`);

      for (const notification of unreadNotifications) {
        // Check if we've already shown a system notification for this one (simple check for now)
        // In a real app, you'd need a more persistent way to track this across sessions/reloads
        // For now, we'll rely on the isRead status and mark as read after showing.

        try {
          // Trigger the system notification
          new Notification(notification.title || '新通知', {
            body: notification.content || '您有一条新通知。',
            icon: '/favicon.ico' // Optional: path to an icon
          });
          console.log(`System notification shown for notification ${notification._id}`);

          // Mark the notification as read on the backend after showing the system notification
          // Need a backend API for this. Assuming apiService.notificationService.markAsRead(notification._id)
          // If such an API exists:
          // try {
          //     await apiService.notificationService.markAsRead(notification._id);
          //     console.log(`Notification ${notification._id} marked as read on backend.`);
          //     // Optionally update the local state to reflect the read status immediately
          //     setNotifications(prevNotifs =>
          //         prevNotifs.map(notif =>
          //             notif._id === notification._id ? { ...notif, isRead: true } : notif
          //         )
          //     );
          // } catch (markReadError) {
          //     console.error(`Failed to mark notification ${notification._id} as read:`, markReadError);
          // }

        } catch (systemNotifError) {
          console.error(`Failed to show system notification for ${notification._id}:`, systemNotifError);
        }
      }
    }
  };

  // Fetch general notifications (replace with your actual notification fetching logic)
  const fetchNotifications = async () => {
    console.log('Fetching notifications...');
    setLoading(true);
    setError('');
    try {
      // Ensure apiService.notificationService.getNotifications() fetches for the current user
      const response = await apiService.notificationService.getNotifications();
      console.log('Fetch notifications response:', response);

      if (response.success && Array.isArray(response.data)) {
        setNotifications(response.data);
        // Trigger system notifications for newly fetched unread items
        triggerSystemNotifications(response.data);

      } else {
         console.error('获取通知失败或数据格式错误:', response);
         setError(response.message || '获取通知失败或数据格式错误');
         setNotifications([]); // Clear notifications on error
      }
    } catch (err) {
      setError(err.message || '获取通知失败');
      console.error('获取通知失败:', err);
       setNotifications([]); // Clear notifications on error
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    // Request permission when the component mounts
    requestNotificationPermission();

    // Call your actual notification fetching function here
    fetchNotifications();

    // For now, using mock data to test the display and delete functionality
    // const mockNotifications = [
    //     { _id: '1', title: '系统公告 1', content: '这是一个重要的系统公告内容。\n请注意！', createdAt: new Date(Date.now() - 86400000), isRead: false }, // 1 day ago, unread
    //     { _id: '2', title: '系统公告 2', content: '这是另一个公告内容。\n关于新功能的发布。\n详细信息请查看...\n换行测试。', createdAt: new Date(Date.now() - 3600000), isRead: true }, // 1 hour ago, read
    //     { _id: '3', title: '信息支付提醒', content: '您购买信息 A 的订单已支付成功。', createdAt: new Date(Date.now() - 60000), isRead: false }, // 1 minute ago, unread
    //     { _id: '4', title: '充值/提现进度提醒', content: '您的提现申请正在处理中。', createdAt: new Date(Date.now() - 10000), isRead: false }, // 10 seconds ago, unread
    //     { _id: '5', title: '信息到期收益到账提醒', content: '您发布的信息 B 的收益已到账。', createdAt: new Date(), isRead: true }, // just now, read
    // ];

    // setLoading(false);
    // setNotifications(mockNotifications);
    // triggerSystemNotifications(mockNotifications);

    // Clear interval/timeout if any were set for real fetching
    // return () => {}; // Cleanup function if needed

  }, []);

   // Handle notification deletion
   const handleDeleteNotification = async (notificationId) => {
       console.log(`Attempting to delete notification: ${notificationId}`);
       try {
           // Call the backend delete notification API
           // Ensure apiService.notificationService.deleteNotification() is for the current user
           const response = await apiService.notificationService.deleteNotification(notificationId);
           console.log('Delete notification API response:', response);
           if (response.success) {
               console.log('Notification deleted successfully on backend');
               // Remove the deleted notification from the state locally
               setNotifications(notifications.filter(notification => notification._id !== notificationId && notification.id !== notificationId));
           } else {
               console.error('删除通知失败:', response.message);
               // Optionally show an error message to the user
           }
       } catch (err) {
           console.error('删除通知请求失败:', err);
           // Optionally show an error message to the user
       }
   };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (error) {
    return (
      <Box sx={{ p: 3 }}>
        <Typography color="error" align="center">
          {error}
        </Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h5" gutterBottom>
        通知
      </Typography>
      {notifications.length === 0 ? (
          <Typography align="center" color="text.secondary">
              暂无通知
          </Typography>
      ) : (
          // Render notifications as Cards
           <Box>
               {notifications.map(notification => (
                   <Card key={notification._id || notification.id} sx={{ mb: 2, position: 'relative' }}> {/* Add position relative for absolute positioning of close button */}
                       <CardContent>
                           {/* Close button */}
                           <IconButton
                               sx={{
                                   position: 'absolute',
                                   top: 8,
                                   right: 8,
                               }}
                               onClick={() => handleDeleteNotification(notification._id || notification.id)}
                               size="small"
                           >
                               <CloseIcon fontSize="small" />
                           </IconButton>

                           <Typography variant="h6" component="div" sx={{ mr: 4 }}> {/* Add margin to avoid overlap with close button */}
                               {notification.title || '无标题'}
                           </Typography>
                           <Typography variant="body2" color="text.secondary">
                               {notification.content || '无内容'}
                           </Typography>
                           {
                               notification.createdAt && (
                                   <Typography variant="caption" color="text.secondary" display="block" sx={{ mt: 1 }}>
                                       {new Date(notification.createdAt).toLocaleString()}
                                   </Typography>
                               )
                           }
                           {/* Display read status for debugging/visibility - remove later if not needed */}
                           <Typography variant="caption" color="text.primary" sx={{ fontWeight: notification.isRead ? 'normal' : 'bold' }}>
                               状态: {notification.isRead ? '已读' : '未读'}
                           </Typography>
                       </CardContent>
                   </Card>
               ))}
           </Box>
      )}
    </Box>
  );
}

export default Notification; 