import React, { useState } from 'react';
import { useNavigate, Link as RouterLink } from 'react-router-dom';
import {
  Box,
  Paper,
  TextField,
  Button,
  Typography,
  Link,
  Alert,
  Card,
  CardContent,
  CircularProgress,
} from '@mui/material';
import { useAuth } from '../contexts/AuthContext';

function Register() {
  const navigate = useNavigate();
  const { register } = useAuth();
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    confirmPassword: '',
    email: '',
    phone: '',
    referrerCode: '',
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (formData.password !== formData.confirmPassword) {
      setError('两次输入的密码不一致');
      return;
    }

    try {
      console.log('开始注册流程，表单数据:', formData);
      setError('');
      setLoading(true);
      const result = await register({
        username: formData.username,
        password: formData.password,
        email: formData.email,
        phone: formData.phone,
        referrerCode: formData.referrerCode,
      });
      console.log('注册结果:', result);
      
      if (result.success) {
        console.log('注册成功，准备跳转到登录页');
        navigate('/login');
      } else {
        console.error('注册失败:', result.error);
        setError(result.error || '注册失败');
      }
    } catch (err) {
      console.error('注册过程出错:', err);
      console.error('错误详情:', {
        message: err.message,
        response: err.response,
        request: err.request
      });
      setError(err.message || '注册失败，请重试');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        backgroundColor: '#f0f0f0',
        padding: 2,
        width: '100%',
        maxWidth: '100vw',
        overflow: 'hidden',
      }}
    >
      <Card 
        sx={{ 
          width: '100%',
          maxWidth: 400,
          padding: { xs: 2, sm: 3 },
          borderRadius: 2,
          boxShadow: 3,
        }}
      >
        <CardContent>
          <Typography 
            variant="h5" 
            component="h2" 
            gutterBottom 
            textAlign="center"
            sx={{ 
              fontSize: { xs: '1.5rem', sm: '1.75rem' },
              fontWeight: 'bold',
              mb: 3
            }}
          >
            用户注册
          </Typography>
          <Box 
            component="form" 
            onSubmit={handleSubmit} 
            sx={{ 
              mt: 2,
              '& .MuiTextField-root': {
                mb: 2
              }
            }}
          >
            <TextField
              label="用户名"
              variant="outlined"
              fullWidth
              value={formData.username}
              onChange={(e) => setFormData({ ...formData, username: e.target.value })}
              required
              autoComplete="username"
              sx={{
                '& .MuiInputBase-root': {
                  height: '48px'
                }
              }}
            />
            <TextField
              label="密码"
              variant="outlined"
              fullWidth
              type="password"
              value={formData.password}
              onChange={(e) => setFormData({ ...formData, password: e.target.value })}
              required
              autoComplete="new-password"
              sx={{
                '& .MuiInputBase-root': {
                  height: '48px'
                }
              }}
            />
            <TextField
              label="确认密码"
              variant="outlined"
              fullWidth
              type="password"
              value={formData.confirmPassword}
              onChange={(e) => setFormData({ ...formData, confirmPassword: e.target.value })}
              required
              autoComplete="new-password"
              sx={{
                '& .MuiInputBase-root': {
                  height: '48px'
                }
              }}
            />
            <TextField
              label="邮箱"
              variant="outlined"
              fullWidth
              type="email"
              value={formData.email}
              onChange={(e) => setFormData({ ...formData, email: e.target.value })}
              required
              autoComplete="email"
              sx={{
                '& .MuiInputBase-root': {
                  height: '48px'
                }
              }}
            />
            <TextField
              label="手机号"
              variant="outlined"
              fullWidth
              value={formData.phone}
              onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
              required
              autoComplete="tel"
              sx={{
                '& .MuiInputBase-root': {
                  height: '48px'
                }
              }}
            />
            <TextField
              label="推荐码（选填）"
              variant="outlined"
              fullWidth
              value={formData.referrerCode}
              onChange={(e) => setFormData({ ...formData, referrerCode: e.target.value })}
              autoComplete="off"
              sx={{
                '& .MuiInputBase-root': {
                  height: '48px'
                }
              }}
            />
            {error && (
              <Typography 
                color="error" 
                variant="body2" 
                sx={{ 
                  mt: 1,
                  mb: 2,
                  textAlign: 'center',
                  fontSize: '0.875rem'
                }}
              >
                {error}
              </Typography>
            )}
            <Button
              type="submit"
              variant="contained"
              fullWidth
              sx={{ 
                mt: 2,
                mb: 2,
                height: '48px',
                fontSize: '1rem',
                fontWeight: 'bold'
              }}
              disabled={loading}
            >
              {loading ? <CircularProgress size={24} /> : '注册'}
            </Button>
            <Box 
              sx={{ 
                display: 'flex', 
                justifyContent: 'center', 
                alignItems: 'center',
                mt: 2
              }}
            >
              <Link 
                href="/login" 
                variant="body2"
                sx={{ 
                  fontSize: '0.875rem',
                  textDecoration: 'none',
                  '&:hover': {
                    textDecoration: 'underline'
                  }
                }}
              >
                已有账号? 立即登录
              </Link>
            </Box>
          </Box>
        </CardContent>
      </Card>
    </Box>
  );
}

export default Register; 