import React, { useEffect, useState, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  Avatar,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListItemSecondaryAction,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  CircularProgress
} from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import WalletIcon from '@mui/icons-material/AccountBalanceWallet';
import LockIcon from '@mui/icons-material/Lock';
import SettingsIcon from '@mui/icons-material/Settings';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import SupportAgentIcon from '@mui/icons-material/SupportAgent';
import { useAuth } from '../contexts/AuthContext';
import apiService, { baseStaticURL } from '../services/api';

// Function to calculate countdown (copied from Notification.js)
const calculateCountdown = (createdAt, period) => {
    const purchaseDate = new Date(createdAt);
    const expiryDate = new Date(purchaseDate.getTime() + period * 24 * 60 * 60 * 1000);
    const now = new Date();
    const diff = expiryDate - now;

    if (diff <= 0) {
      return '已过期';
    }

    const days = Math.floor(diff / (1000 * 60 * 60 * 24));
    const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((diff % (1000 * 60)) / 1000);

    return `${days}天${hours}小时${minutes}分${seconds}秒`;
  };

function Profile() {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [balance, setBalance] = useState(0);
  const [purchasedInfos, setPurchasedInfos] = useState([]); // State for purchased infos
  const [loadingPurchased, setLoadingPurchased] = useState(true); // Loading state for purchased infos
  const [errorPurchased, setErrorPurchased] = useState(''); // Error state for purchased infos


  useEffect(() => {
    loadBalance();
    fetchPurchasedInfos(); // Fetch purchased infos on mount
  }, []);

  async function loadBalance() {
    try {
      const data = await apiService.transactionService.getBalance();
      if (typeof data.data?.balance === 'number') {
        setBalance(data.data.balance);
      } else {
        console.error('获取余额接口返回的数据格式错误或余额不是数字:', data);
        setBalance(0);
      }
    } catch (err) {
      console.error('加载余额失败:', err);
      setBalance(0);
    }
  }

  // Fetch purchased infos (copied from Notification.js)
  const fetchPurchasedInfos = useCallback(async () => {
    setLoadingPurchased(true);
    setErrorPurchased('');
    try {
      const response = await apiService.postService.getPurchasedInfos();
      if (response.success) {
        setPurchasedInfos(response.data);
      } else {
         setErrorPurchased(response.message || '获取已购买信息失败');
      }
    } catch (err) {
      setErrorPurchased(err.message || '获取已购买信息失败');
      console.error('获取已购买信息失败:', err);
    } finally {
      setLoadingPurchased(false);
    }
  }, []);

   // 每秒更新倒计时 (copied from Notification.js)
  useEffect(() => {
    const timer = setInterval(() => {
      setPurchasedInfos(prevInfos => [...prevInfos]);
    }, 1000);

    return () => clearInterval(timer);
  }, [purchasedInfos]); // Re-run effect if purchasedInfos changes

    // Handle row click to navigate to detail page (copied from Notification.js)
  const handleRowClick = (infoId) => {
    navigate(`/detail/${infoId}`);
  };


  return (
    <Box sx={{ bgcolor: '#f7f8fa', minHeight: '100vh', pb: 8 }}>
      {/* 用户信息卡片 */}
      <Card sx={{ mb: 2, borderRadius: 0 }}>
        <CardContent>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Avatar
              src={user?.avatar}
              sx={{ width: 56, height: 56, mr: 2 }}
            />
            <Box sx={{ flex: 1 }}>
              <Typography variant="h6">{user?.username || '未登录'}</Typography>
              <Typography variant="body2" color="text.secondary">
                {user?.phone ? user.phone : (user ? '未设置手机号' : '点击登录')}
              </Typography>
              {/* Display Balance */}
               {user && (
                  <Typography variant="body2" color="text.primary" sx={{ mt: 1 }}>
                      用户ID: {user._id}
                  </Typography>
              )}
            </Box>
            <IconButton onClick={() => navigate('/customer-service')}>
              <SupportAgentIcon />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {/* 已购买信息表格 */}
       <Box sx={{ p: 2 }}>
          <Typography variant="h6" gutterBottom>
              已购买信息
          </Typography>
           {loadingPurchased ? (
              <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 200 }}>
                 <CircularProgress />
              </Box>
           ) : errorPurchased ? (
              <Typography color="error" align="center">
                 {errorPurchased}
              </Typography>
           ) : (
              <TableContainer component={Paper}>
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ padding: '8px' }}>标题</TableCell>
                      <TableCell sx={{ padding: '8px' }}>购买时间</TableCell>
                      <TableCell sx={{ padding: '8px', fontWeight: 'bold', color: 'green' }}>收益</TableCell>
                      <TableCell sx={{ padding: '8px' }}>倒计时</TableCell>
                       <TableCell sx={{ padding: '8px' }}>状态</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {purchasedInfos.map((info) => (
                      <TableRow
                        key={info._id}
                        onClick={() => handleRowClick(info._id)}
                        sx={{ cursor: 'pointer' }}
                      >
                        <TableCell sx={{ padding: '8px' }}>{info.title}</TableCell>
                        <TableCell sx={{ padding: '8px' }}>
                          {new Date(info.createdAt).toLocaleString()}
                        </TableCell>
                        <TableCell sx={{ padding: '8px', fontWeight: 'bold', color: 'green' }}>{info.repaymentAmount || 'N/A'}</TableCell>
                        <TableCell sx={{ padding: '8px' }}>
                          {calculateCountdown(info.createdAt, info.period)}
                        </TableCell>
                         <TableCell sx={{ padding: '8px' }}>
                           {info.isPublic ? '已公开' : '未公开'}
                         </TableCell>
                      </TableRow>
                    ))}
                    {purchasedInfos.length === 0 && (
                      <TableRow>
                        <TableCell colSpan={5} align="center">
                          暂无已购买信息
                        </TableCell>
                      </TableRow>
                    )}
                  </TableBody>
                </Table>
              </TableContainer>
           )}

       </Box>

      {/* Removed Wallet Card and Settings/Payment Password List */}
    </Box>
  );
}

export default Profile; 