import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  CircularProgress,
  List,
  ListItem,
  ListItemText,
  Divider,
  Avatar,
} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import HeadsetMicIcon from '@mui/icons-material/HeadsetMic';
import apiService, { baseStaticURL } from '../services/api';
import { useAuth } from '../contexts/AuthContext';

// Assuming WebSocket server is running on ws://localhost:3001
// const websocketUrl = 'ws://fw108ck86325.vicp.fun/ws'; // Use your penetration address
const websocketUrl = 'wss://fw108ck86325.vicp.fun/ws'; // WebSocket URL

function CustomerService() {
  const navigate = useNavigate();
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const { user } = useAuth();
  const [ws, setWs] = useState(null);

  useEffect(() => {
    loadMessages();

    // Setup WebSocket connection
    const websocket = new WebSocket(websocketUrl);
    setWs(websocket);

    websocket.onopen = () => {
      console.log('WebSocket connected');
      // Authenticate WebSocket connection
      const token = localStorage.getItem('token');
      if (token) {
        websocket.send(JSON.stringify({
          type: 'IDENTIFY',
          token: token,
          role: 'user', // Identify as a user
        }));
      }
    };

    websocket.onmessage = (event) => {
      const message = JSON.parse(event.data);
      console.log('WebSocket message received:', message);

      switch (message.type) {
        case 'CUSTOMER_SERVICE_MESSAGE':
          // Add new message to the state if it's from admin for this user
          // We should check if the message is for the current user, although backend should handle this
          // A simple check based on senderType could be useful here
          if (message.data && message.data.senderType === 'admin') {
              setMessages(prevMessages => 
                  [...prevMessages, message.data].sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt))
              );
          }
          break;
        // Handle other message types if needed
        default:
          console.log('Unknown WebSocket message type:', message.type);
      }
    };

    websocket.onerror = (error) => {
      console.error('WebSocket error:', error);
      // Optionally show an error message to the user
    };

    websocket.onclose = (event) => {
      console.log('WebSocket disconnected', event.code, event.reason);
      // Attempt to reconnect if the disconnection is not intentional
      if (event.code !== 1000) { // 1000 is normal closure
          console.log('Attempting to reconnect WebSocket...');
          // Implement a reconnect strategy (e.g., with a delay)
      }
    };

    // Cleanup function to close WebSocket on unmount
    return () => {
      console.log('Closing WebSocket connection');
      websocket.close();
    };
  }, []); // Empty dependency array means this effect runs once on mount

  const loadMessages = async () => {
    setLoading(true);
    setError('');
    try {
      const data = await apiService.userService.getCustomerServiceMessages();
      if (Array.isArray(data)) {
        setMessages(data.sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt)));
      } else {
        console.error('Failed to fetch messages or data format is incorrect:', data);
        setMessages([]);
        setError('加载消息失败或数据格式错误');
      }
    } catch (err) {
      setError(err.message || '加载消息失败');
      console.error('加载消息失败:', err);
      setMessages([]);
    } finally {
      setLoading(false);
    }
  };

  const handleSend = async () => {
    if (!newMessage.trim()) return;

    const optimisticMessage = {
      _id: Date.now(),
      content: newMessage,
      createdAt: new Date().toISOString(),
      isUser: true,
      senderType: 'user', // Add senderType for consistent message structure
    };

    setMessages([...messages, optimisticMessage].sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt)));
    setNewMessage('');
    setLoading(true);
    setError('');

    try {
      const response = await apiService.userService.sendCustomerServiceMessage({
        content: optimisticMessage.content,
      });
      console.log('Send message response:', response);
      if (response && response._id) {
        setMessages(prevMessages => prevMessages
          .filter(msg => msg._id !== optimisticMessage._id)
          .concat(response) // Use the actual message from backend
          .sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt))
        );
      } else {
        console.error('Failed to send message or backend response format is incorrect:', response);
        setError(response?.message || '发送失败');
        // Optionally remove the optimistic message if sending failed
         setMessages(prevMessages => prevMessages.filter(msg => msg._id !== optimisticMessage._id));
      }
    } catch (err) {
      console.error('发送消息请求失败:', err);
      setError(err.message || '发送失败');
      // Optionally remove the optimistic message if sending failed
       setMessages(prevMessages => prevMessages.filter(msg => msg._id !== optimisticMessage._id));
    } finally {
      setLoading(false);
    }
  };

  const handleBackClick = () => {
    navigate(-1);
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
      {/* Removed Header - Relying on App Layout Header */}

      {/* Messages Area */}
      <Box sx={{ flexGrow: 1, overflowY: 'auto', p: 2 }}>
         {/* Add a title within the content area if needed */}
         <Typography variant="h6" gutterBottom textAlign="center">正在与客服聊天</Typography>

        {loading && messages.length === 0 ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
            <CircularProgress />
          </Box>
        ) : error ? (
          <Typography color="error" align="center">
            {error}
          </Typography>
        ) : messages.length === 0 ? (
          <Typography align="center" color="text.secondary">
            开始和客服聊天吧！
          </Typography>
        ) : (
          <List sx={{ width: '100%', p: 0 }}>
            {messages.map((message) => (
              <ListItem key={message._id || message.createdAt} sx={{
                justifyContent: message.isUser ? 'flex-end' : 'flex-start',
                p: 1,
              }}>
                <Box sx={{ display: 'flex', alignItems: 'flex-start', flexDirection: message.isUser ? 'row-reverse' : 'row', gap: 1 }}>
                  {message.isUser ? (
                    <Avatar src={user?.avatar} sx={{ width: 32, height: 32 }} />
                  ) : (
                    <HeadsetMicIcon sx={{ width: 32, height: 32, color: 'text.secondary' }} />
                  )}

                  <Box sx={{
                    bgcolor: message.isUser ? '#e0f7fa' : '#f0f0f0',
                    borderRadius: '15px',
                    p: 1.5,
                    maxWidth: '70%',
                    wordBreak: 'break-word',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: message.isUser ? 'flex-end' : 'flex-start',
                  }}>
                    <Typography variant="body1">{message.content}</Typography>
                    <Typography variant="caption" color="text.secondary" sx={{ mt: 0.5, fontSize: '0.7rem' }}>
                      {new Date(message.createdAt).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', second: '2-digit' })}
                    </Typography>
                  </Box>
                </Box>
              </ListItem>
            ))}
          </List>
        )}
      </Box>

      <Box sx={{ p: 2, borderTop: '1px solid #e0e0e0', display: 'flex', gap: 1, alignItems: 'center' }}>
        <TextField
          fullWidth
          variant="outlined"
          placeholder="请输入消息..."
          value={newMessage}
          onChange={(e) => setNewMessage(e.target.value)}
          onKeyPress={(e) => {
            if (e.key === 'Enter' && !e.shiftKey) {
              e.preventDefault();
              handleSend();
            }
          }}
          disabled={loading}
          size="small"
        />
        <Button
          variant="contained"
          color="primary"
          onClick={handleSend}
          disabled={loading || !newMessage.trim()}
          endIcon={<SendIcon />}
          sx={{ flexShrink: 0 }}
        >
          发送
        </Button>
      </Box>
    </Box>
  );
}

export default CustomerService; 