import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  CircularProgress,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
} from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import apiService, { baseStaticURL } from '../services/api';

const withdrawMethods = [
  { value: 'alipay', label: '支付宝' },
  { value: 'bank', label: '银行卡' },
];

function Withdraw() {
  const navigate = useNavigate();
  const [amount, setAmount] = useState('');
  const [withdrawMethod, setWithdrawMethod] = useState('alipay');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [showPasswordDialog, setShowPasswordDialog] = useState(false);
  const [password, setPassword] = useState('');
  const [balance, setBalance] = useState(0);

  // State for收款信息
  const [receiverName, setReceiverName] = useState('');
  const [accountNumber, setAccountNumber] = useState('');
  const [accountType, setAccountType] = useState('');
  const [qrCodeFile, setQrCodeFile] = useState(null);

  // State for payment password status
  const [isPaymentPasswordSet, setIsPaymentPasswordSet] = useState(false);
  const [statusLoading, setStatusLoading] = useState(true); // To indicate if status is being loaded

  React.useEffect(() => {
    loadBalance();
    checkPaymentPasswordStatus(); // Check payment password status on load
  }, []);

  const loadBalance = async () => {
    try {
      const data = await apiService.transactionService.getBalance();
      // Access balance from the nested data object
      setBalance(data.data?.balance);
    } catch (err) {
      setError(err.message || '加载余额失败');
    }
  };

  const checkPaymentPasswordStatus = async () => {
      try {
          setStatusLoading(true);
          const response = await apiService.paymentPasswordService.getPaymentPasswordStatus();
           // Assuming the response data has a field indicating the status, e.g., isSet
           // Need to confirm the actual response structure from backend
          setIsPaymentPasswordSet(response.data?.hasPayPassword || false); // Adjust based on actual response field
          setStatusLoading(false);
      } catch (err) {
          console.error('Error checking payment password status:', err);
          setError('加载支付密码状态失败');
          setStatusLoading(false);
      }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (statusLoading) { // Prevent submission while status is loading
        setError('支付密码状态加载中，请稍候...');
        return;
    }

     if (!isPaymentPasswordSet) {
         setError(
             <>请先设置支付密码。前往<a href="/set-payment-password">设置页面</a></> // Placeholder link
         );
         return;
     }

    if (!amount || isNaN(amount) || Number(amount) <= 0) {
      setError('请输入有效的提现金额');
      return;
    }

    if (Number(amount) > balance) {
      setError('提现金额不能大于可用余额');
      return;
    }

    // Add validation for new fields
    if (!receiverName || !accountNumber || !accountType) {
        setError('请填写完整的收款信息');
        return;
    }

    // QR code file is optional based on the screenshot layout, but we should confirm if it's required by backend.
    // For now, let's assume it might be sent if selected.

    setShowPasswordDialog(true);
  };

  const handleConfirmWithdraw = async () => {
    if (!password) {
      setError('请输入支付密码');
      return;
    }

    setLoading(true);
    setError('');

    try {
      // Verify payment password before proceeding
      const verificationResponse = await apiService.paymentPasswordService.verifyPaymentPassword(password);

      if (!verificationResponse.success) {
          setError(verificationResponse.message || '支付密码验证失败');
          setLoading(false);
          // Do not close dialog on verification failure, let user try again
          return;
      }

        // Include new fields in the withdrawal request
        const requestData = new FormData();
        requestData.append('amount', Number(amount));
        requestData.append('type', withdrawMethod); // Match backend field name
        requestData.append('receiver', receiverName); // Match backend field name
        requestData.append('receiveAccount', accountNumber); // Match backend field name
        if(qrCodeFile) {
             requestData.append('qrcode', qrCodeFile); // Match backend multer field name
        }

        // Need to confirm the correct API endpoint and method for withdrawal submission
        // Based on previous search, it might be apiService.transactionService.createWithdraw
        // Let's use that for now, and adjust if needed.

        await apiService.transactionService.createWithdraw(requestData); // Assuming createWithdraw handles FormData

        navigate('/profile', { state: { message: '提现申请已提交' } });
    } catch (err) {
      setError(err.message || '提现失败');
      setShowPasswordDialog(false);
    } finally {
      setLoading(false);
    }
  };

  const handleQrCodeFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setQrCodeFile(file);
    }
  };

  return (
    <Box sx={{ bgcolor: '#f7f8fa', minHeight: '100vh' }}>
      <Box sx={{ maxWidth: 600, mx: 'auto', mt: 3, p: 2 }}>
        {/* User Info Card - Placeholder */}
        <Card sx={{ mb: 3 }}>
          <CardContent>
            <Typography variant="h6" gutterBottom>用户信息</Typography>
            {/* Add user info fields here based on backend data when available */}
            <Typography variant="body1">账号: huangy</Typography>
            <Typography variant="body1">ID: 6832fd3a9d3c60fd5fbaf917</Typography>
            <Typography variant="body1">手机:</Typography>
            <Typography variant="body1">邮箱:</Typography>
             <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                {/* console.log('Withdraw.js rendering balance:', balance) */}
                可用余额：{loading ? '加载中...' : `¥${balance?.toFixed(2) || '0.00'}`}
              </Typography>
          </CardContent>
        </Card>

        {/* Payment Info Card */}
        <Card sx={{ mb: 3 }}>
          <CardContent>
            <Typography variant="h6" gutterBottom>收款信息</Typography>
             <TextField
                fullWidth
                label="收款人"
                value={receiverName}
                onChange={(e) => setReceiverName(e.target.value)}
                sx={{ mb: 2 }}
              />
              <TextField
                fullWidth
                label="收款账号"
                value={accountNumber}
                onChange={(e) => setAccountNumber(e.target.value)}
                 sx={{ mb: 2 }}
              />
               <TextField
                fullWidth
                label="收款类型"
                value={accountType}
                onChange={(e) => setAccountType(e.target.value)}
              />
          </CardContent>
        </Card>

        {/* QR Code Card - Placeholder */}
         <Card sx={{ mb: 3 }}>
          <CardContent sx={{ textAlign: 'center' }}>
             <Typography variant="h6" gutterBottom>收款码</Typography>
             {/* Add QR code image display here */}
              {qrCodeFile ? (
                <Box sx={{ width: 180, height: 180, mx: 'auto' }}>
                    <img src={URL.createObjectURL(qrCodeFile)} alt="收款二维码" style={{ width: '100%', height: '100%', objectFit: 'contain' }} />
                </Box>
              ) : (
                 <Box sx={{ width: 180, height: 180, bgcolor: 'grey.300', mx: 'auto', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                     <Typography variant="body2" color="text.secondary">暂无收款码</Typography>
                 </Box>
              )}

             <input
                type="file"
                accept="image/*"
                style={{ display: 'none' }}
                id="qr-code-upload"
                onChange={handleQrCodeFileChange}
             />
             <label htmlFor="qr-code-upload">
                <Button variant="contained" component="span" sx={{ mt: 2 }}>点击更换</Button>
             </label>

          </CardContent>
        </Card>

        {/* Withdrawal Amount and Password Card */}
        <Card>
          <CardContent>
            <Typography variant="h6" gutterBottom>
                提现金额
              </Typography>
              {/* Keep balance display here as it's related to withdrawal amount */}
             
              <TextField
                fullWidth
                type="number"
                label="请输入提现金额"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                InputProps={{
                  startAdornment: <Typography sx={{ mr: 1 }}>¥</Typography>,
                }}
                sx={{ mb: 3 }}
              />

               <TextField
                fullWidth
                type="password"
                label="请输入支付密码"
                 value={password}
                onChange={(e) => setPassword(e.target.value)}
              />

              {error && (
                <Typography color="error" sx={{ mb: 2 }}>
                  {error}
                </Typography>
              )}

              {/* The submit button will trigger the password dialog */}
              <Button
                fullWidth
                variant="contained"
                onClick={handleSubmit}
                disabled={loading}
                sx={{ mt: 2 }}
              >
                {loading ? <CircularProgress size={24} /> : '确认提现'}
              </Button>
            {/* Remove the form tag from here as we are using a single submit button outside the fields */}
          </CardContent>
        </Card>
      </Box>

      {/* Password Dialog remains the same for now */}
      <Dialog open={showPasswordDialog} onClose={() => setShowPasswordDialog(false)}>
        <DialogTitle>请输入支付密码</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            fullWidth
            type="password"
            label="支付密码"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            margin="dense"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setShowPasswordDialog(false)}>取消</Button>
          <Button onClick={handleConfirmWithdraw} disabled={loading}>
            {loading ? <CircularProgress size={24} /> : '确认'}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default Withdraw; 