import React, { useState } from 'react';
import { Box, Typography, TextField, Button, Card, CardContent, CircularProgress, Checkbox, FormControlLabel, Link, IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { Home as HomeIcon } from '@mui/icons-material';

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const { login } = useAuth();

  const handleSubmit = async (event) => {
    event.preventDefault();
    setError('');
    setLoading(true);
    
    if (!username || !password) {
      setError('用户名和密码不能为空');
      setLoading(false);
      return;
    }

    try {
      console.log('开始登录流程，用户名:', username);
      const result = await login(username, password);
      console.log('登录结果:', result);
      
      if (result.success) {
        console.log('登录成功，准备跳转到首页');
        navigate('/home');
      } else {
        console.error('登录失败:', result.error);
        setError(result.error || '登录失败');
      }
    } catch (err) {
      console.error('登录过程出错:', err);
      console.error('错误详情:', {
        message: err.message,
        response: err.response,
        request: err.request
      });
      setError(err.message || '登录失败，请重试');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        backgroundColor: '#f0f0f0',
        padding: 2,
        width: '100%',
        maxWidth: '100vw',
        overflow: 'hidden',
        position: 'relative'
      }}
    >
      <IconButton
        onClick={() => navigate('/home')}
        sx={{
          position: 'absolute',
          top: 16,
          right: 16,
          backgroundColor: 'white',
          boxShadow: 2,
          '&:hover': {
            backgroundColor: '#f5f5f5'
          }
        }}
      >
        <HomeIcon />
      </IconButton>
      <Card 
        sx={{ 
          width: '100%',
          maxWidth: 400,
          padding: { xs: 2, sm: 3 },
          borderRadius: 2,
          boxShadow: 3,
        }}
      >
        <CardContent>
          <Typography 
            variant="h5" 
            component="h2" 
            gutterBottom 
            textAlign="center"
            sx={{ 
              fontSize: { xs: '1.5rem', sm: '1.75rem' },
              fontWeight: 'bold',
              mb: 3
            }}
          >
            用户登录
          </Typography>
          <Box 
            component="form" 
            onSubmit={handleSubmit} 
            sx={{ 
              mt: 2,
              '& .MuiTextField-root': {
                mb: 2
              }
            }}
          >
            <TextField
              label="用户名"
              variant="outlined"
              fullWidth
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              required
              autoComplete="username"
              sx={{
                '& .MuiInputBase-root': {
                  height: '48px'
                }
              }}
            />
            <TextField
              label="密码"
              variant="outlined"
              fullWidth
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
              autoComplete="current-password"
              sx={{
                '& .MuiInputBase-root': {
                  height: '48px'
                }
              }}
            />
            <FormControlLabel
              control={
                <Checkbox 
                  checked={rememberMe} 
                  onChange={(e) => setRememberMe(e.target.checked)}
                  sx={{ '& .MuiSvgIcon-root': { fontSize: 24 } }}
                />
              }
              label="记住密码"
              sx={{ mt: 1, mb: 2 }}
            />
            {error && (
              <Typography 
                color="error" 
                variant="body2" 
                sx={{ 
                  mt: 1,
                  mb: 2,
                  textAlign: 'center',
                  fontSize: '0.875rem'
                }}
              >
                {error}
              </Typography>
            )}
            <Button
              type="submit"
              variant="contained"
              fullWidth
              sx={{ 
                mt: 2,
                mb: 2,
                height: '48px',
                fontSize: '1rem',
                fontWeight: 'bold'
              }}
              disabled={loading}
            >
              {loading ? <CircularProgress size={24} /> : '登录'}
            </Button>
            <Box 
              sx={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                alignItems: 'center',
                mt: 2
              }}
            >
              <Link 
                href="#" 
                variant="body2" 
                onClick={() => console.log('Forgot Password')}
                sx={{ 
                  fontSize: '0.875rem',
                  textDecoration: 'none',
                  '&:hover': {
                    textDecoration: 'underline'
                  }
                }}
              >
                忘记密码
              </Link>
              <Link 
                href="/register" 
                variant="body2"
                sx={{ 
                  fontSize: '0.875rem',
                  textDecoration: 'none',
                  '&:hover': {
                    textDecoration: 'underline'
                  }
                }}
              >
                没有账号? 立即注册
              </Link>
            </Box>
          </Box>
        </CardContent>
      </Card>
    </Box>
  );
}

export default Login; 