import axios from 'axios';
import config from '../config';

const baseURL = `${config.apiBaseUrl}/api`;
const baseStaticURL = config.apiBaseUrl;

const api = axios.create({
    baseURL,
    timeout: 30000,
    headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
    }
});

// 请求拦截器
api.interceptors.request.use(
    (config) => {
        console.log('发送请求:', {
            url: config.url,
            method: config.method,
            data: config.data,
            headers: config.headers,
            baseURL: config.baseURL,
            fullURL: `${config.baseURL}${config.url}`
        });
        const token = localStorage.getItem('token');
        if (token) {
            config.headers.Authorization = `Bearer ${token}`;
        }
        return config;
    },
    (error) => {
        console.error('请求拦截器错误:', error);
        return Promise.reject(error);
    }
);

// 响应拦截器
api.interceptors.response.use(
    (response) => {
        console.log('收到响应:', {
            status: response.status,
            data: response.data,
            headers: response.headers,
            config: {
                url: response.config.url,
                method: response.config.method,
                baseURL: response.config.baseURL
            }
        });
        return response.data;
    },
    (error) => {
        console.error('响应错误:', error);
        
        if (error.response) {
            console.error('错误状态码:', error.response.status);
            console.error('错误数据:', error.response.data);
            console.error('请求配置:', {
                url: error.config.url,
                method: error.config.method,
                baseURL: error.config.baseURL,
                headers: error.config.headers
            });
            
            switch (error.response.status) {
                case 401:
                    localStorage.removeItem('token');
                    localStorage.removeItem('user');
                    window.location.href = '/login';
                    break;
                case 403:
                    console.error('没有权限访问该资源');
                    break;
                case 404:
                    console.error('请求的资源不存在');
                    break;
                case 500:
                    console.error('服务器内部错误');
                    break;
                default:
                    console.error('请求失败:', error.response.data);
            }
        } else if (error.request) {
            console.error('网络错误: 没有收到响应');
            console.error('请求配置:', {
                url: error.config.url,
                method: error.config.method,
                baseURL: error.config.baseURL,
                headers: error.config.headers,
                timeout: error.config.timeout
            });
        } else {
            console.error('请求配置错误:', error.message);
        }
        
        return Promise.reject(error);
    }
);

// API 服务
const apiService = {
    // 测试连接
    testConnection: async () => {
        try {
            console.log('开始测试连接...');
            const testUrl = `${config.apiBaseUrl}/test`;
            console.log('测试URL:', testUrl);
            
            const response = await fetch(testUrl, {
                method: 'GET',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                mode: 'cors',
                cache: 'no-cache',
                credentials: 'same-origin'
            });
            
            console.log('收到响应:', {
                status: response.status,
                statusText: response.statusText,
                headers: Object.fromEntries(response.headers.entries())
            });

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            
            const data = await response.json();
            console.log('测试连接成功:', data);
            return data;
        } catch (error) {
            console.error('测试连接失败:', error);
            if (error.message === 'Failed to fetch') {
                throw new Error('连接失败：请检查服务器是否运行、网络连接是否正常、防火墙是否允许访问3003端口');
            }
            throw error;
        }
    },

    // 用户服务
    userService: {
        login: (data) => api.post('/auth/login', data),
        register: (data) => api.post('/auth/register', data),
        getProfile: () => api.get('/user/profile'),
        updateProfile: (data) => api.put('/user/profile', data),
        uploadAvatar: (formData) => api.post('/user/avatar', formData, {
            headers: {
                'Content-Type': 'multipart/form-data',
            },
        }),
        getCustomerServiceMessages: () => api.get('/customer-service/messages'),
        sendCustomerServiceMessage: (data) => api.post('/customer-service/messages', data),
    },

    // 帖子服务
    postService: {
        getPosts: (params) => api.get('/info/list', { params }),
        getPostDetails: (id) => api.get(`/info/${id}`),
        createPost: (data) => api.post('/info', data),
        updatePost: (id, data) => api.put(`/info/${id}`, data),
        deletePost: (id) => api.delete(`/info/${id}`),
        getMyPosts: () => api.get('/info/my'),
        searchPosts: (query) => api.get('/info/search', { params: { query } }),
        purchaseInfo: (id, data) => api.post(`/info/${id}/purchase`, data),
        getPurchasedInfos: () => api.get('/info/purchased'),
    },

    // 交易服务
    transactionService: {
        getBalance: () => api.get('/transactions/balance'),
        createRecharge: (data) => api.post('/transactions/recharge', data),
        createWithdraw: (data) => api.post('/wallet/withdraw', data),
        getTransactions: (params) => api.get('/transactions', { params }),
        getTransactionHistory: () => api.get('/transactions/history'),
    },

    // 充值服务
    rechargeService: {
        getRechargePaths: () => api.get('/app/recharge/paths'),
        submitRechargeOrder: (formData) => api.post('/app/recharge', formData, {
            headers: {
                'Content-Type': 'multipart/form-data',
            },
        }),
    },

    // 支付密码服务
    paymentPasswordService: {
        getPaymentPasswordStatus: () => api.get('/user/check-pay-password'),
        setPaymentPassword: (passwordData) => {
            if (typeof passwordData === 'object' && passwordData !== null && passwordData.password !== undefined) {
                return api.post('/user/set-pay-password', passwordData);
            } else {
                return api.post('/user/set-pay-password', { password: passwordData });
            }
        },
        verifyPaymentPassword: (password) => api.post('/user/verify-pay-password', { payPassword: password }),
    },

    // 通知服务
    notificationService: {
        getNotifications: () => api.get('/notifications'),
        markAsRead: (id) => api.put(`/notifications/${id}/read`),
        getUnreadCount: () => api.get('/notifications/unread/count'),
        deleteNotification: (id) => api.delete(`/notifications/${id}`),
    },

    // 管理员服务
    adminService: {
        getDashboard: () => api.get('/api/admin'),
        getUsers: (params) => api.get('/api/admin/users', { params }),
        deleteUser: (id) => api.delete(`/api/admin/users/${id}`),
        updateUserRole: (id, role) => api.put(`/api/admin/users/${id}/role`, { role }),
        getTransactions: () => api.get('/api/admin/transactions'),
        reviewTransaction: (id, data) => api.post(`/api/admin/transactions/${id}/review`, data),
        getTransactionNotifications: () => api.get('/api/admin/transactions/notifications'),
    }
};

export { apiService, baseStaticURL };