import React, { createContext, useContext, useState, useEffect } from 'react';
import apiService, { baseStaticURL } from '../services/api';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState({
    username: '测试用户',
    role: 'user',
    balance: 0
  });
  const [loading, setLoading] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    setLoading(false);
  }, []);

  const login = async (username, password) => {
    setLoading(true);
    try {
      console.log('尝试登录:', { username, password });
      const response = await apiService.userService.login({ username, password });
      console.log('登录响应:', response);
      
      if (!response || !response.token) {
        console.error('登录响应无效:', response);
        throw new Error('登录响应无效');
      }

      localStorage.setItem('token', response.token);
      console.log('Token已保存');

      if (response.user) {
        localStorage.setItem('user', JSON.stringify(response.user));
        setUser({ ...response.user, token: response.token });
        console.log('用户信息已保存');
      }

      setIsAuthenticated(true);
      setLoading(false);
      return { success: true };
    } catch (err) {
      console.error('登录失败:', err);
      setError(err.message || '登录失败');
      setLoading(false);
      return { success: false, error: err.message || '登录失败' };
    }
  };

  const register = async (userData) => {
    setLoading(true);
    try {
      const response = await apiService.userService.register(userData);
      localStorage.setItem('token', response.token);
      localStorage.setItem('user', JSON.stringify(response.user));
      setUser({ ...response.user, token: response.token });
      setIsAuthenticated(true);
      setLoading(false);
      return { success: true, data: response };
    } catch (err) {
      console.error('注册失败:', err);
      setError(err.message || '注册失败');
      setLoading(false);
      return { success: false, error: err.message || '注册失败' };
    }
  };

  const logout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    setUser({
      username: '测试用户',
      role: 'user',
      balance: 0
    });
    setIsAuthenticated(true);
  };

  const updateUserProfile = async (profileData) => {
    setLoading(true);
    try {
      const response = await apiService.userService.updateProfile(profileData);
      const updatedUser = { ...user, ...response.data };
      localStorage.setItem('user', JSON.stringify(updatedUser));
      setUser(updatedUser);
      setLoading(false);
      return { success: true, data: response.data };
    } catch (err) {
      console.error('更新资料失败:', err);
      setError(err.message || '更新资料失败');
      setLoading(false);
      return { success: false, error: err.message || '更新资料失败' };
    }
  };

  const isAdmin = () => {
    return user && user.role === 'admin';
  };

  const value = {
    user,
    loading,
    isAuthenticated,
    error,
    login,
    register,
    logout,
    updateUserProfile,
    isAdmin,
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 