import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import Layout from './components/Layout';

// 页面组件
import Splash from './pages/Splash';
import Login from './pages/Login';
import Register from './pages/Register';
import Home from './pages/Home';
import Profile from './pages/Profile';
import ProfileEdit from './pages/ProfileEdit';
import MyPosts from './pages/MyPosts';
import Detail from './pages/Detail';
import RechargePaths from './pages/RechargePaths';
import Recharge from './pages/Recharge';
import Withdraw from './pages/Withdraw';
import Notification from './pages/Notification';
import TransactionNotification from './pages/TransactionNotification';
import CustomerService from './pages/CustomerService';
import Settings from './pages/Settings';
import SetPaymentPassword from './pages/SetPaymentPassword';

// 创建主题
const theme = createTheme({
  palette: {
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
  // 添加移动端适配
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          minHeight: '44px', // 移动端按钮最小高度
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiInputBase-root': {
            minHeight: '44px', // 移动端输入框最小高度
          },
        },
      },
    },
  },
});

// 受保护的路由组件
const ProtectedRoute = ({ children }) => {
  const { isAuthenticated } = useAuth();
  return isAuthenticated ? children : <Navigate to="/login" />;
};

// 添加移动端检测
const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AuthProvider>
        <Router basename={process.env.PUBLIC_URL}>
          <Routes>
            {/* 公共路由 */}
            <Route path="/" element={<Home />} />
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
            
            {/* 受保护的路由 */}
            <Route element={<Layout />}>
              <Route path="/home" element={<Home />} />
              <Route path="/profile" element={<Profile />} />
              <Route path="/profile/edit" element={<ProfileEdit />} />
              <Route path="/my-posts" element={<MyPosts />} />
              <Route path="/recharge-paths" element={<RechargePaths />} />
              <Route path="/recharge-paths/:pathId" element={<Recharge />} />
              <Route path="/withdraw" element={<Withdraw />} />
              <Route path="/notification" element={<Notification />} />
              <Route path="/transaction-notification" element={<TransactionNotification />} />
              <Route path="/customer-service" element={<CustomerService />} />
              <Route path="/settings" element={<Settings />} />
              <Route path="/set-payment-password" element={<SetPaymentPassword />} />
            </Route>

            {/* 处理所有其他路由，返回首页 */}
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </Router>
      </AuthProvider>
    </ThemeProvider>
  );
}

export default App; 